/* eslint-disable */

body {
  font-family: 'Mulish', sans-serif;
  font-size: 18px;
  margin: 0;
  box-sizing: border-box;
  
  
}

a:link, a:visited {
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-weight: bold;


}

a:hover, a:active {
 text-decoration: none;
}

/*MENU*/

#menu {
  color: rgb(255, 255, 255);
  position: fixed;
  top: 60px;
  right: 85px;
  width: 60px;
  height: 30px;
  cursor: pointer;
  z-index: 400;}


#menu span {
  display: block;
  position: absolute;
  height: 4px;
  background-color:#ffffff;
  left: 0;
  transform: rotate(
0deg
);
  transition: .25s ease-in-out;}

#menu span:first-child {
  width: 100%;
  top: 0;
}

#menu span:nth-child(2) {
  width: 100%;
  top: 10px;
}
#menu span:nth-child(3) {
  width: 80%;
  top: 20px;
  right: 0;
  left: auto;
}

#menu:hover span {
background-color: rgb(168, 38, 255);
}



.info {
  position: fixed;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  margin-top: 30vh;
  padding: 5vw;
  z-index: 500;
  width: fit-content;
  height: fit-content;
  color: #fff;
}

.info span {
  display:inline-block;
  width:0.05vw;
  background-color: rgb(255, 255, 255);
  height:300%;
  
}


.firstContainer .loaded {
  
 transition: transform 2s cubic-bezier(.81,.04,.16,.97);
 transform: scale(1.5);
 position: absolute;
 top: 35vh;
 left: 27vw;
 z-index: 1000;


}

.thirdContainer .loaded {
  
